



























import naData from 'template/no_data'
import loading from 'template/loading'
export default {
	components: {
		naData,
		loading
	},
	data() {
		return {
			list: [],
			total: 0,
			nodata: false,
			nodataText: '您还没有团队成员~',
			isLoading: false,
		};
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.list = []
			this.getList()
			uni.stopPullDownRefresh()
		}, 800);
	},
	onLoad(options) {
		this.showLoading()
		this.getList()
	},
	methods: {
		getList() {
			var that = this
			this.$api.get('/api/member/myGroup', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.total = res.data.data.total
					var list = res.data.data.list
					setTimeout(function() {
						if (list.length == 0) {
							that.nodata = true
						} else {
							that.list = list
						}
					}, 700)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
		showLoading() {
			this.isLoading = true
			setTimeout(() => {
				this.isLoading = false
			}, 800);
		},
	}
}
